import React from "react";
//import api from "../api";
import "../css/Userlist.css";

export class Userlist extends React.Component {
  constructor() {
    super();
  }
  componentDidMount() {
    this.loadData();
  }

  loadData() {
    // api.get("users").then(
    //   (result) => {
    //     console.log(result);
    //     this.Userlist = result.data;
    //     this.setState({
    //       data: "success",
    //     });
    //   },
    //   (error) => {
    //     console.error(error);
    //   }
    // );
    fetch("http://localhost:3001/users")
      .then((res) => res.json())
      .then(
        (result) => {
          console.log(result);
          this.Userlist = result.data;
          this.setState({
            data: "success",
          });
        },
        (error) => {
          console.error(error);
        }
      );
  }
  //Userlist = [1, 22, 3, 4, 5];
  Userlist = [];

  onDelete(id) {
    // api.delete("users" + id).then(
    //   (result) => {
    //     console.log(result);
    //   },
    //   (error) => {
    //     console.error(error);
    //   }
    // );
    const requestOptions = {
      method: "DELETE",
      headers: { "Content-Type": "application/json" },
    };
    fetch("http://localhost:3001/users/" + id, requestOptions)
      .then((res) => res.json())
      .then(
        (result) => {
          console.log(result);
        },
        (error) => {
          console.error(error);
        }
      );
  }
  eachcard(e) {
    let id = e._id;
    let imgSrc = e.image
      ? e.image
      : "data:image/png;base64,";
    return (
      <div className="eachUser" key={e.userName}>
        <img src={imgSrc}></img>
        <p>{e.userName}</p>
        <p>{e.email}</p>
        <button onClick={() => this.onDelete(id)}>Delete</button>
      </div>
    );
  }

  render() {
    // let display = this.eachcard();
    let display = this.Userlist.map((e) => this.eachcard(e));
    return <div className="userList">{display}</div>;
  }
}
