import React from "react";
import "../css/resetPassword.css";

export class ResetPassword extends React.Component {
  reset() {
    let userName = document.getElementById("userName").value;
    let chngPassword = document.getElementById("resetPassWord").value;
    let passWord = document.getElementById("passWord").value;
    // to do validation
    let data = { userName, chngPassword, passWord };
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(data),
    };
    fetch("http://localhost:3001/users", requestOptions)
      .then((res) => res.json())
      .then(
        (result) => {
          console.log(result);
          //this.ResetPassword = result.data;
          // this.setState({
          //  data: "success",
          // });
        },
        (error) => {
          console.error(error);
        }
      );
  }

  render() {
    return (
      <div className="myform">
        <div className="loginPage">
          <div>
            <span>User Name:</span>
            <span>
              <input type="text" name="name" id="userName"></input>
            </span>
          </div>
          <div>
            <span>Old Password:</span>
            <span>
              <input type="password" name="password" id="passWord"></input>
            </span>
          </div>
          <div>
            <span>New Password:</span>
            <span>
              <input type="password" name="password" id="resetPassWord"></input>
            </span>
          </div>
          <button id="signUp" onClick={() => this.reset()}>
            Change Password
          </button>
        </div>
      </div>
    );
  }
}
