import React, { Component } from "react";
import "../css/product.css";
import Clock from "./clock";

export class Product extends React.Component {
  fileds = [
    { id: "title", required: true, type: "text" },
    { id: "type", required: true, type: "text" },
    { id: "description", required: true, type: "textarea" },
    { id: "filename", required: false, type: "text" },
    { id: "height", required: true, type: "number" },
    { id: "width", required: true, type: "number" },
    { id: "price", required: true, type: "number" },
    { id: "rating", required: true, type: "number" },
  ];
  updata = {};
  constructor(props) {
    super(props);
    this.state = {};
  }

  emptyCheckandDataWrap(filed) {
    this.updata[filed.id] = document.getElementById([filed.id]).value;
    if (this.updata[filed.id] === null || this.updata[filed.id] == "") {
      return filed.id + " should not be null or empty/n";
    } else {
      return "";
    }
  }

  updateData() {
    let errors = this.fileds.map((each) => this.emptyCheckandDataWrap(each));
    console.log(errors);
    let errorMsg = errors.join('');
    errorMsg==''?this.sendRequest(this.updata):alert(errorMsg);
  }
  sendRequest(updata) {
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(updata),
    };
    fetch("http://localhost:3001/products/product", requestOptions)
      .then((res) => res.json())
      .then(
        (result) => {
          console.log(result);
        },
        (error) => {
          console.error(error);
        }
      );
  }

  eachFields(filed) {
    let control = <input type={filed.type} id={filed.id} />;
    if (filed.type === "textarea") {
      control = (
        <textarea name={filed.id} id={filed.id} cols="30" rows="10"></textarea>
      );
    }
    return (
      <div className="form-group" key={filed.id}>
        <label>{filed.id}</label>
        {control}
      </div>
    );
  }

  render() {
    let productfields = this.fileds.map((each) => this.eachFields(each));
    return (
      <div className="form-container">
        <Clock></Clock>
        {productfields}
        <button
          type="submit"
          className="signupbtn"
          onClick={() => this.updateData()}
        >
          Create
        </button>
        {/* <button type="submit" className="signupbtn" onClick={() => this.updateData()}>update</button> */}
      </div>
    );
  }
}

export default Product;
