import React from "react";
import "../css/Signup.css";
//import Api from "../api.js";

export class Signup extends React.Component {
  signValidation() {
    var sname = document.getElementById("s_userName").value;
    var semail = document.getElementById("s_email").value;
    var spassword = document.getElementById("s_password").value;
    var cpassword = document.getElementById("c_password").value;
    //alert(spassword);

    var userSignup = { sname, semail, spassword };
    console.log(userSignup);

    if (sname == null || sname == "") {
      alert("Name can't be empty");
    } else if (semail == null || semail == "") {
      alert("enter email");
    } else if (spassword.length < 6) {
      alert("Password must be at least 6 characters.");
    } else if (spassword == cpassword) {
      alert("Password must be at least 6 characters.");
    }
    this.sendRequest(userSignup);
  }
  sendRequest(userSignup) {
    // api
    //   .post("users/signup", userSignup)
    //   .then((result) => {
    //     console.log(result);
    //   })
    //   .catch((error) => {
    //     console.error(error);
    //   });
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(userSignup),
    };
    fetch("http://localhost:3001/users/signup", requestOptions)
      .then((res) => res.json())
      .then(
        (result) => {
          console.log(result);
        },
        (error) => {
          console.error(error);
        }
      );
  }

  render() {
    return (
      <div className="modal">
        <form className="modal-content">
          <div className="container">
            <h1>Sign Up</h1>
            <div className="brdr"></div>

            <label>
              <b>UserName</b>
            </label>
            <input
              type="text"
              id="s_userName"
              placeholder="Enter Your Name"
              name="username"
            ></input>

            <label>
              <b>Email</b>
            </label>
            <input
              type="text"
              id="s_email"
              placeholder="Enter Email"
              name="email"
            ></input>

            <label>
              <b>Password</b>
            </label>
            <input
              type="password"
              id="s_password"
              placeholder="Enter Password"
              name="psw"
            ></input>

            <label>
              <b>Repeat Password</b>
            </label>
            <input
              type="password"
              id="c_password"
              placeholder="Repeat Password"
              name="psw-repeat"
            ></input>
          </div>
        </form>
        <button
          type="submit"
          className="signupbtn"
          onClick={() => this.signValidation()}
        >
          Sign Up
        </button>
      </div>
    );
  }
}
