import React, { Component } from "react";

class Clock extends Component {
  constructor(props) {
    super(props);
    this.state = { // initialise the default state
      now: new Date(),
    };
   // this.setTimer();
  }
  componentDidMount() { // called when a component mount is done
      this.setTimer();
  }
  
  setTimer = () => {
    this.timer = setInterval(() => this.tick(), 1000); // normal arrow function call this keyword will work in tick
    // this.timer = setInterval(this.tick(), 1000);    // normal function call this keyword will not work in tick
    // this.timer = setInterval(this.tick, 1000); // function as a reference
  };
  tick = () => {
    console.log("hi");
    this.setState({ now: new Date() }); //component render every time when setstate call
  };
  render() {
    return <p>{this.state.now.toLocaleString()}</p>; // date object should not be used directly
    // return <p>{this.state.now}</p>; // invalid example
  }
  componentWillUnmount() {  // called when a component destroyed(life cycle)
      console.log("components destroyed");
      clearInterval(this.timer);
  }
}

export default Clock;
