import React from "react";
//import "../css/Productlist.css";
import styles from "../css/Productlist.module.css";

export class Productlist extends React.Component {
  constructor() {
    super();
  }
  componentDidMount() {
    this.loadData();
  }

  loadData() {
    fetch("http://localhost:3001/products")
      .then((res) => res.json())
      .then(
        (result) => {
          console.log(result);
          this.Productlist = result.data;
          this.setState({
            data: "success",
          });
        },
        (error) => {
          console.error(error);
        }
      );
  }
  //Userlist = [1, 22, 3, 4, 5];
  Productlist = [];

  onDelete(id) {
    const requestOptions = {
      method: "DELETE",
      headers: { "Content-Type": "application/json" },
    };
    fetch("http://localhost:3001/products/" + id, requestOptions)
      .then((res) => res.json())
      .then(
        (result) => {
          console.log(result);
        },
        (error) => {
          console.error(error);
        }
      );
  }
  eachcard(e) {
    let id = e._id;
    return (
      <div className={styles.eachUser+" " +styles[e.type]} key={e.title}>
        <p>{e.title}</p>
        <p>{e.type}</p>
        <button onClick={() => this.onDelete(id)}>
          <i className="fas fa-trash"></i>Delete
        </button>
      </div>
    );
  }

  render() {
    // let display = this.eachcard();
    let display = this.Productlist.map((e) => this.eachcard(e));
    return <div className="userList">{display}</div>;
  }
}
